.about-us {
  @include pageGrid;

  &__title {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 1 / 4;
    }

    @include onDesktop {
      grid-column: span 5;
    }
  }

  &__description {
    grid-column: 1 / -1;
    text-align: left;
    font-size: 23px;
    line-height: 32px;
    font-weight: 500;

    @include onTablet {
      grid-column: 4 / 7;
    }

    @include onDesktop {
      grid-column: span 5;
    }
  }
}
